.projectsWrapper {
  position: absolute;
  width: 400px;
  top: 20px;
  left: 20px;
  display: flex;
  flex-direction: column;
  border: 1px solid rgba(198, 234, 252, 0.9);
  box-shadow: 0 0 3px hsla(180, 100%, 75%, 0.7);
  border-style: outset;
  cursor: move;
}

.projectsBorderTop {
  height: 20px;
  background: rgba(198, 234, 252, 0.9);
  cursor: move;
}

.projects {
  background: black;
  color: rgba(198, 234, 252, 0.9);
  font-size: 30px;
  font-weight: 900;
}

.projectsText {
  user-select: none;
  text-align: center;
  border-bottom: 1px solid rgba(198, 234, 252, 0.9);
  padding: 10px;
  color: rgba(198, 234, 252, 0.9);
}

.projectsList {
  height: 400px;
  overflow: auto;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.projectsList::-webkit-scrollbar {
  width: 5px;
}

.projectsList::-webkit-scrollbar-thumb {
  background-color: rgb(174, 178, 187);
}
.projectsList::-webkit-scrollbar-track {
  border-left: 1px solid rgba(198, 234, 252, 0.9);
}

.productItem {
  display: flex;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  color: rgba(198, 234, 252, 0.9);
  border-bottom: 1px rgba(198, 234, 252, 0.9) solid;
}

.productItem:last-child {
  border-bottom: none;
}

.productItem:hover {
  background: rgba(198, 234, 252, 0.9);
  color: black;
  cursor: pointer;
}

.productItemLogo {
  width: 50px;
}

.productDetailContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.productDetailName {
  display: flex;
  justify-content: center;
  color: rgba(198, 234, 252, 0.9);
}

.productDetailImage {
  display: flex;
  justify-content: center;
}

.productDetailImage img {
  width: 90%;
  border: 1px solid rgba(198, 234, 252, 0.9);
  border-radius: 2px;
}

.productDetailLink {
  display: flex;
  justify-content: center;
}

.productDetailLogo {
  width: 100px;
}

.productDetailLink a {
  margin-top: 20px;
  font-size: large;
  color: rgba(198, 234, 252, 0.9);
  /* text-decoration: none; */
}

.productDescription {
  align-self: center;
  font-size: 15px;
  display: flex;
  justify-content: center;
  width: 90%;
  color: rgba(198, 234, 252, 0.9);
}

.productDescription p {
  display: flex;
  text-align: justify;
}

.productTopic {
  display: flex;
  width: 90%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.productTopicHeader {
  color: rgba(198, 234, 252, 0.9);
}

.productTopicBody {
  display: flex;
  justify-content: center;
  gap: 5px;
  flex-wrap: wrap;
}

.topicTag {
  display: flex;
  font-size: 15px;
  padding: 5px;
  border: 1px solid rgba(198, 234, 252, 0.9);
  border-radius: 10px;
  color: rgba(198, 234, 252, 0.9);
  /* margin: 0px 5px 0px 5px; */

  transition: 0.2s;
}

.topicTag:hover {
  color: #000;
  background: rgba(198, 234, 252, 0.9);
}

@media only screen and (max-width: 975px) {
  body {
    overflow: visible;
  }

  .projectsWrapper {
    margin-top: 10px;
    width: 100%;
    position: relative;
    right: 0px;
    top: 0px;
  }
}
