.hackingFbi {
  display: flex;
  justify-content: center;
  align-items: center;
  width: fit-content;
  height: fit-content;
  position: absolute;
  bottom: 40px;
  right: 20px;
  padding: 0px;
  border: none;
  user-select: none;
}

.overlayLabel {
  font-size: 2rem;
  color: rgba(198, 234, 252);
  position: absolute;
  right: 15%;
  display: flex;
  gap: 20px;
  justify-content: center;
  align-items: center;
  opacity: 0.4;
}

.overlayLabel * {
  display: flex;
}

.accessGranted {
  text-align: center;
  color: #b1e2fa;
  font-size: 50px;
}

.hackingFbiText {
  text-align: center;
  user-select: none;
}

@media only screen and (max-width: 975px) {
  .hackingFbi {
    display: none;
  }
}
