.projectsWrapper {
  position: absolute;
  width: 400px;
  top: 20px;
  right: 20px;
  display: flex;
  flex-direction: column;
  border: 1px solid rgba(198, 234, 252, 0.9);
  box-shadow: 0 0 3px hsla(180, 100%, 75%, 0.7);
  border-style: outset;
  cursor: move;
}

.projectsBorderTop {
  height: 20px;
  background: rgba(198, 234, 252, 0.9);
}

.projects {
  background: black;
  color: rgba(198, 234, 252, 0.9);
  font-size: 30px;
  font-weight: 900;
}

.projectsText {
  user-select: none;
  text-align: center;
  border-bottom: 1px solid rgba(198, 234, 252, 0.9);
  padding: 10px;
  color: rgba(198, 234, 252, 0.9);
}

.projectsList {
  height: 400px;
  overflow: auto;
  display: flex;
  flex-wrap: wrap;
}
.projectsList::-webkit-scrollbar {
  width: 5px;
}

.projectsList::-webkit-scrollbar-thumb {
  background-color: rgb(174, 178, 187);
}
.projectsList::-webkit-scrollbar-track {
  border-left: 1px solid rgba(198, 234, 252, 0.9);
}

.project {
  width: 100%;
  height: 100px;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  color: rgba(198, 234, 252, 0.9);
  border-bottom: 1px rgba(198, 234, 252, 0.9) solid;
}

.project:last-child {
  border-bottom: none;
}

.project:hover {
  background: rgba(198, 234, 252, 0.9);
  color: black;
  cursor: pointer;
}

@media only screen and (max-width: 975px) {
  body {
    overflow: visible;
  }

  .projectsWrapper {
    margin-top: 10px;
    width: 100%;
    position: relative;
    right: 0px;
    top: 0px;
  }
}
