.terminal {
  width: 650px;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 20px;
  background: transparent;
  color: rgba(198, 234, 252, 0.5);
  resize: none;
  outline: none;
  border: none;
  font-size: 16px;
  font-family: "Terminal";
  padding: 10px;
  cursor: auto;
}

.terminalAuto {
  width: 500px;
  height: 350px;
  top: 450px;
  left: 20px;
}

.terminal::-webkit-scrollbar {
  display: none;
}

@media only screen and (max-width: 975px) {
  .terminal {
    display: none;
  }
}