.svg-frame {
    /* border: 1px solid #fff; */
	position: relative;
	width: 200px;
	height: 200px;
	transform-style: preserve-3d;
	display: flex;
	justify-content: center;
	align-items: center;
}

.scifi-loader {
	position: absolute;
	transition: 0.5s;
	transform-origin: center;
	width: 450px;
	height: 450px;
	fill: none;
	filter: drop-shadow(0 0 12px #b1e2fa);
    opacity: 0.3;
}

#big-centro,
#outter1,
#solo-lines,
#center,
#outter-center,
#bottom-dots,
#center-lines,
#squares,
#top-dots {
	transform-origin: center;
	animation: rotate 4s ease-in-out infinite alternate;
}

#big-centro {
	animation-delay: -1.5s;
}

#outter1 {
	animation-delay: -1.2s;
}

#center {
	animation-delay: -2.2s;
}

#bottom-dots,
#top-dots {
	animation-duration: 7s;
}

#center-lines,
#outter-center {
	animation-duration: 6s;
	animation-delay: -3s;
}

@keyframes rotate {
	to {
		transform: rotate(360deg);
	}
}
