.hackingFbi {
  color: rgba(198, 234, 252, 0.9);
  position: absolute;
  top: 700px;
  left: 700px;
  font-size: 30px;
  background: black;
  border: 1px solid rgba(198, 234, 252, 0.9);
  padding: 10px;
  border-top: 20px solid rgba(198, 234, 252, 0.9);
  border-style: outset;
  user-select: none;
  box-shadow: 0 0 3px hsla(180, 100%, 75%, 0.7);
  cursor: move;
}

.hackingFbi::after {
  display: block;
  content: "";
  width: 20px;
  height: 20px;
  position: absolute;
  top: -25px;
  right: -5px;
  border-top: 2px solid rgba(198, 234, 252, 0.9);
  border-right: 2px solid rgba(198, 234, 252, 0.9);
}

.corner::before {
  display: block;
  content: "";
  width: 20px;
  height: 20px;
  position: absolute;
  bottom: -5px;
  left: -5px;
  border-bottom: 2px solid rgba(198, 234, 252, 0.9);
  border-left: 2px solid rgba(198, 234, 252, 0.9);
}

.accessGranted {
  text-align: center;
  color: rgba(198, 234, 252, 0.9);
  font-size: 50px;
}

.hackingFbiText {
  text-align: center;
  user-select: none;
}

@media only screen and (max-width: 975px) {
  .hackingFbi {
    display: none;
  }
}