.app {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.globe {
  width: 300px;
  border-radius: 50%;
}

.ilyahacker {
  border-collapse: separate;
  width: 600px;
  top: 20px;
  left: 800px;
  /* background: rgb(174, 178, 187); */
  background: rgba(198, 234, 252, 0.9);
  position: absolute;
  border: 1px solid hsla(180, 100%, 75%, 0.07);
  border-top: 20px solid hsla(180, 100%, 75%, 0.07);
  border-style: outset;
  box-shadow: 0 0 3px hsla(180, 100%, 75%, 0.7);
  cursor: move;
}


.footerMobile {
  display: none;
}

.overlay {
  position: fixed;
  width: 100%;
  opacity: 0.5;
}

@media only screen and (max-width: 975px) {
  .app {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: unset;
    padding: 2px;
  }

  .globe {
    display: none;
  }

  .ilyahacker {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
  }

  .footerMobile {
    display: block;
  }

  .footerDesktop {
    display: none;
  }
}


@keyframes refresh {
  0% {
    bottom: 100%
  }

  70% {
    bottom: 100%
  }

  100% {
    bottom: 0px
  }
}

.refresh {
  float: left;
  width: 200%;
  height: 100px;
  z-index: 8;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0) 0, rgba(255, 255, 255, .4) 10%, rgba(0, 0, 0, .1) 100%);
  opacity: .1;
  position: fixed;
  bottom: 0;
  animation: refresh 8s linear infinite;
  animation-name: refresh;
  animation-duration: 8s;
  animation-timing-function: linear;
  animation-delay: 0s;
  animation-iteration-count: infinite;
  animation-direction: normal;
  animation-fill-mode: none;
  animation-play-state: running;
}

