.cligno {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 2;
  background: repeating-linear-gradient(90deg, rgba(255, 0, 0, 0.08) 0.01px 1px, rgba(0, 255, 0, 0.08) 1px 2px, rgba(0, 0, 255, 0.08) 2px 3px), repeating-linear-gradient(0deg, transparent 0.001px 1px, rgba(0, 0, 0, 0.4) 1px 2px) !important;
  opacity: 0.9;
  pointer-events: none;
  /* animation: flicker 0.02s infinite; */
}