.modal {
    z-index: 1 !important;
    border: 1px solid rgba(198, 234, 252, 0.9);
    min-height: 400px;
}

.modal-mask, .modal-mask-in {
    z-index: 0 !important;
    background: transparent !important;
}

.modal-header {
    background: rgba(198, 234, 252, 0.9) !important;
}

.modal-body {
    background: black;
}

.modal-footer {
    background: rgba(198, 234, 252, 0.9) !important;
}

.modal-footer-btnList {
    display: none;
}